import get from 'lodash/get';

export const selectCurrentWorkspace = (state) => get(state, 'bogcode.workspaceSelected', '');
export const selectShowWorkspaceValidator = (state) => get(state, 'bogcode.showWorkspaceValidator', false);
export const selectEmail = (state) => get(state, 'bogcode.email', '');
export const selectUrlReturn = (state) => get(state, 'bogcode.urlReturn', ''); // url que determina a donde ir luego de loguearse.
export const selectIsAdmin = (state) => get(state, 'bogcode.role', '') === 'admin';
export const selectUserName = (state) => get(state, 'bogcode.userName', '');
export const selectApiVersion = (state) => get(state, 'bogcode.apiVersion', '');
export const selectWsPort = (state) => get(state, 'bogcode.wsPort', 0);
export const selectBogcodeLoading = (state) => get(state, 'workspace.isFetching', 0);
